      </el-header>
      <el-container>
        <el-aside width="300px"  style="height: 90vh">
          <el-scrollbar style="height: 100%;" wrapStyle="overflow-x:hidden;">
            <el-menu active-text-color="#409EFF" default-active="2-1-1">

              <el-submenu index="1">
                <template #title>
                  <i class="el-icon-s-data"></i>
                  <span>Database Information</span>
                </template>
                <el-submenu v-for="(value, table) in tableList" :key="table" :index="table">
                  <template #title>{{ table }}</template>
                  <el-menu-item v-for="(colType, col) in value" :key="col" :index="col">
                    {{ col }} ({{ colType }})
                  </el-menu-item>
                </el-submenu>
              </el-submenu>

              <el-submenu index="6" v-show="false">
                <template #title>
                  <i class="el-icon-s-operation"></i>
                  <span>Method</span>
                </template>
                  <el-menu-item index="6-1">
                    <el-radio-group v-model="methodRadio" @change="methodChange">
                      <el-radio class="smallRightMargin" :label="0">TIPS</el-radio>
                      <el-radio class="smallRightMargin" :label="1">Cost</el-radio>
                      <el-radio class="smallRightMargin" :label="2">Random</el-radio>
                    </el-radio-group>
                  </el-menu-item>
              </el-submenu>

              <el-submenu index="2">
                <template #title>
                  <i class="el-icon-s-order"></i>
                  <span>Parameter</span>

                </template>
                <el-menu-item-group class="largeMenuItem">
                  <el-menu-item index="2-1-1" class="noHighLight">
                    <el-form label-width="90px" label-position="left" :rules="formRule" :model="queryForm" size="mini">
                      <el-form-item label="Model" v-show="methodRadio === 0">
                        <el-switch v-model="queryForm.mode" active-text="I-TIPS" inactive-text="B-TIPS" > </el-switch>
                      </el-form-item>
                      <el-form-item label="Tree Edit dist">
                        <el-switch v-model="queryForm.editFlag"></el-switch>
                      </el-form-item>

                      <el-form-item label="Number" v-show="!queryForm.mode">
                        <el-input v-model="queryForm.number"></el-input>
                      </el-form-item>
                      <el-form-item label="S_weight" >
                        <el-input v-model="queryForm.s_weight"></el-input>
                      </el-form-item>
                      <el-form-item label="C_weight" >
                        <el-input v-model="queryForm.c_weight"></el-input>
                      </el-form-item>
                      <el-form-item label="Cost_weight" >
                        <el-input v-model="queryForm.cost_weight"></el-input>
