              <el-row class="el-row">
                <el-input type="textarea" placeholder="Input your SQL or select an example SQL" :autosize="{minRows:10, maxRows:20}" v-model="SQLstatement"  />
              </el-row>


              <el-row :gutter="10" class="el-row">
                <el-col :span="7" class="el-col">
                  <el-select v-model="selectedSQL" @change="loadSQLFile($event)" @clear="clearTextarea" placeholder="Example SQL" clearable>
                    <el-option v-for="singleFile in SQLFiles" :value="singleFile" :key="singleFile">
                      {{singleFile}}
                    </el-option>
                  </el-select>
                </el-col>

                <el-col :span="2" class="el-col">
                  <el-button :loading="isLoading" @click="submitSQL" type="primary">Execute</el-button>
                </el-col>
              </el-row>
              <el-row class="el-row">
                <el-col class="el-col">
                  <el-card v-if="tableData.length > 0" class="box-card" shadow="hover">
                    <el-table v-if="tableData.length > 0"
                              :data="tableData"
                              highlight-current-row
                              @row-click="handleTableSelect"
                              style="width: 100%"
                              :default-sort="{prop:'number'}"
                              stripe>

                      <el-table-column type="expand">
                        <template v-slot="props">
                          <el-form inline>
                            <el-form-item label="Cost">
                              <span>{{ props.row.cost }}</span>
                            </el-form-item>
                          </el-form>
                        </template>
                      </el-table-column>
                      <el-table-column width="60px" prop="number" label="Id" sortable></el-table-column>
                      <el-table-column prop="s_dist" label="S_dist" sortable></el-table-column>
                      <el-table-column prop="c_dist" label="C_dist" sortable></el-table-column>
                      <el-table-column prop="cost_dist" label="Cost_dist"></el-table-column>
                      <el-table-column prop="relevance" label="Relevance"></el-table-column>
                    </el-table>
                  </el-card>
                </el-col>
              </el-row>
            </el-col>
            <el-col :span="15" class="el-col">
              <el-card class="box-card" v-show="needToDraw != null">
                <draw-tree ref="drawTree"></draw-tree>
              </el-card>
            </el-col>
          </el-row>

        </el-main>
      </el-container>
    </el-container>
  </div>
</template>

<script>
import DrawTree from "@/components/DrawTree";

export default {
  name: 'HelloWorld',
  components: {DrawTree},
  data () {
    return {
      dbName: 'school',
      tableList: {},
      SQLstatement: '',
      selectedSQL: '',
      SQLFiles: [],
      isLoading: false,
      tableData: [],




      isComparePlan: false,
      isDifferentColor: true,
      isEarlyAccess: false,
      userSuggestion: '',
      queryForm: {mode: false, editFlag: false, s_weight: 0.33, c_weight:0.33, cost_weight:0.33, lambda: 0.5, number: 5, filter: 0.5},
      formRule : {
        number: [{type: 'number', min: 0,  max:20}],
      },
      needToDraw: null,
      methodRadio: 0,
    }
  },
  methods: {
    methodChange(val) {
      if(val !== 0)
      {
        this.queryForm.mode = false;
      }
    },

    getSQLFiles() {
      this.$axios.get("/sqlFile")
        .then((response)=> {
          for(var i in response.data){
            this.SQLFiles.push(response.data[i]);
          }
          this.SQLFiles.sort(function (a,b) {
            return Number(a.split('.')[0]) - Number(b.split('.')[0]);
          })
        })
        .catch((error)=> {
          console.log(error);
          this.SQLFiles = [];
        });
    },

    getDBInfo() {
      this.$axios.get("/databaseInfo/"+this.dbName )
        .then((response)=> {
          this.tableList = JSON.parse(response.data)
        })
        .catch((error)=> {
          console.log(error);
          this.tableList = {"I can't get the database infomation." : {"Cat't": "get dbInfo"}}
        });

    },


    getCookie(target) {
      let strcookie = document.cookie;
      let arrcookie = strcookie.split("; ")
      for(let s of arrcookie){
        let arr = s.split("=");
        if (arr[0] === target) {
          return arr[1]
        }
      }
      return ""
    },

    checkForm() {

      let keyList = ["s_weight", "c_weight", "cost_weight", "lambda", "filter"];
      for (let key of keyList)
      {
        let value = this.queryForm[key];
        if (value === undefined || value.length === 0)
        {
          this.$message({message: key + ' is empty!', showClose:true, type:'warning'});
          return false;
        }

        value = parseFloat(value);
        if(value < 0 || value > 1)
        {
          this.$message({message: key + ' range is [0, 1]', showClose:true, type:'warning'});
          return false;
        }
      }
      return true;
    },

    clearTextarea() {
      this.clearCookie("pid");
      this.SQLstatement = '';
      this.tableData = [];
      this.needToDraw = null;
    },

    loadSQLFile($event) {
      this.clearCookie("pid");

      if (this.selectedSQL.length > 0){
        // console.log(this.selectedSQL);
        var targetURL = '/sqlFile/' + this.selectedSQL;

        this.$axios.get(targetURL )
          .then((response)=> {
            // console.log(response)
            this.SQLstatement = response.data;
          })
          .catch((error)=> {
            console.log("I can't get the content of sql file, the name is: "  + this.selectedSQL);
            console.log(error);
            this.SQLstatement = "I can't load the sql file ╮(╯-╰)╭";
          });

        this.tableData = [];
        this.needToDraw = null;
      }
    },


    submitSQL() {
      if (this.SQLstatement === ''){
        this.$message({message: 'Please input the SQL!', showClose:true, type:'warning'});
        return;
      }
      if(! this.checkForm())
      {
        return;
      }
      this.isLoading = true;


      let formData = new FormData();
      let statement = this.SQLstatement.toLowerCase();

      let lineList = statement.split('\n');
      for(let i=0;i<lineList.length;i++) {

        let line = lineList[i]
        let commentIndex = line.indexOf("--")
        if (commentIndex !== -1){
          line = line.substring(0, commentIndex)
        }
        lineList[i] = line
      }
      statement = lineList.join('\n')
      statement = statement.replace(/\s+/g, ' ');
      formData.append('statement', statement);
      formData.append('fileName', this.selectedSQL);
      for(let key in this.queryForm){
        formData.append(key, this.queryForm[key])
      }
      formData.append('method', this.methodRadio);


      let queryMode = this.queryForm['mode'];
      let tempFromInfo = this.queryForm['mode'].toString()+this.queryForm['s_weight'].toString()+this.queryForm['editFlag'].toString()+
        this.queryForm['c_weight'].toString()+this.queryForm['cost_weight'].toString() + this.queryForm['lambda'].toString();
      if(tempFromInfo !== localStorage.getItem('formInfo'))
      {
        this.clearCookie("pid");
        localStorage.setItem('formInfo', tempFromInfo);
      }


      this.$axios.post('/search', formData, {
        header: {
          'Content-Type': 'application/x-www-form-urlencoded',
        }
      }).then((response)=> {
        console.log(response.data)


        if(queryMode === false) {
          this.tableData = [];
          this.clearCookie("pid");
        }

        if(queryMode === true)
        {
          let serverFlag = response.data.flag;
          if(serverFlag['clear'] === true)
          {
            this.tableData = [];
          }
          response.data = [[this.tableData.length, response.data.data]];

          if(serverFlag['end'] === true)
          {
            this.$message({message: 'No more plan', showClose:true, type:'warning'});
            this.isLoading = false;
            return;
          }
        }

        let tempRes;
        let tempObject;
        for(let i in response.data){
            let tempTree = {rootId: null, nodes: [], links: [], TABLE: {}, SCAN: {}, JOIN: {}, diffInfo: null};
            tempRes = {number: response.data[i][0]};
            tempObject = JSON.parse(response.data[i][1]);
            // tempRes.cost = Math.round(tempObject.cost);
            tempRes.cost = tempObject.cost.toFixed(1);
            tempRes.c_dist = tempObject.c_dist.toFixed(2);
            tempRes.s_dist = tempObject.s_dist.toFixed(2);
            tempRes.cost_dist = tempObject.cost_dist.toFixed(2);
            tempRes.relevance = tempObject.relevance.toFixed(2);
            tempRes.workerType = tempObject.workerType;
            localStorage[tempRes.number] = tempRes.workerType;

            let counter=1;
            let content = tempObject.content;
            content.parent = "-1";
            let queue = [content];
            let timeStamp = new Date().getTime().toString();
            tempTree.rootId = this.selectedSQL + "_" + tempRes.number + "_" + "1" + "_" + content.name+ "_" + timeStamp;
            while(queue.length > 0){
              let size = queue.length;
              for(let j=0;j<size;j++)
              {
                let top = queue.shift();

                let node = {id: this.selectedSQL + "_" + tempRes.number + "_" + counter.toString() + '_' + top.name + '_' + timeStamp,
                            text: top.name + "\nCost:" + top.cost.toFixed(1), width:100, height:50, data:{info:''}};
                if (top.info !== ''){
                  node.data.info = top.info;
                }
                top.id = node.id;


                if(top.tag === "TABLE")
                {
                  tempTree.TABLE[node.id] = true;
                  node.text = top.name;
                } else if (top.tag === "SCAN")
                {
                  tempTree.SCAN[node.id] = true;
                } else if (top.tag === "JOIN")
                {
                  tempTree.JOIN[node.id] = true;
                }

                counter++;
                tempTree.nodes.push(node);
                if (top.parent !== "-1"){
                  let edge = {from: top.parent, to:node.id};
                  tempTree.links.push(edge);
                }

                if (top.child_flag){
                  for (let c of top.child){
                    c.parent = node.id;
                    queue.push(c);
                  }
                }
              }
            }

            tempTree.diffInfo = this.generateDiffInfo(tempObject.content);

            tempRes.tree = tempTree;
            this.tableData.push(tempRes);
        }
        this.isLoading = false;
      }).catch((error)=> {
        this.$message({message: 'SQL Execution Failed', showClose:true, type:'error'});
        this.isLoading = false;
        console.log(error);
      })
    },

    // // 发送反馈信息给服务器
    // submitFeedback(){
    // // 首先判断反馈的信息是否合法
    //   // 信息填写是否完整
    //   if (this.bestPlanId.length === 0 || this.bestPlanReason.length === 0
    //   || this.worstPlanId.length === 0 || this.worstPlanReason.length === 0){
    //     this.$message({message: 'Please Complete the Information', showClose:true, type:'error'});
    //     return;
    //   }
    //   // 还需要判断当前是否执行了一个 SQL
    //   if (this.selectedSQL === ''){
    //     this.$message({message: 'Please execute a SQL!', showClose:true, type:'error'});
    //     return;
    //   }
    //
    // // 发送信息
    //   // 构造表单
    //   let formData = new FormData();
    //   formData.append('uid', this.getCookie('uid'));
    //   formData.append('fileName', this.selectedSQL);
    //   formData.append("bestPlanId", this.bestPlanId);
    //   formData.append("bestPlanReason", this.bestPlanReason);
    //   formData.append("worstPlanId", this.worstPlanId);
    //   formData.append("worstPlanReason", this.worstPlanReason);
    //
    //   // 发送
    //   this.$axios.post('/feedback', formData, {
    //     header: {
    //       'Content-Type': 'application/x-www-form-urlencoded',
    //     }
    //   }).then(()=> {
    //     console.log('发送反馈信息成功');
    //     this.$message({message: "thank you for your feedback!", showClose:true, type:'success'})
    //     this.bestPlanId = '';
    //     this.bestPlanReason = '';
    //     this.worstPlanId = '';
    //     this.worstPlanReason = '';
    //     this.isLoading = false;
    //   }).catch((error)=> {
    //     this.$message({message: "Can't send the feedback.", showClose:true, type:'error'});
    //     console.log('发送反馈失败!');
    //     console.log(error);
    //   })
    // },

    submitSuggest() {
      if (this.userSuggestion === ''){
        this.$message({message: 'suggestion is empty!', showClose:true, type:'warning'});
        return;
      }

      let formData = new FormData();
      formData.append('uid', this.getCookie('uid'));
      formData.append('suggestion', this.userSuggestion);

      this.$axios.post('/suggest', formData, {
        header: {
          'Content-Type': 'application/x-www-form-urlencoded',
        }
      }).then(()=> {
        this.$message({message: "thank you for your suggestion!", showClose:true, type:'success'})
        this.userSuggestion = '';
      }).catch((error)=> {
        this.$message({message: "Can't send the suggestion.", showClose:true, type:'error'});
        console.log(error);
      })
    },

    handleTableSelect(val) {
      if (this.isComparePlan === false){

        this.needToDraw = val.tree;
        for(let i =0;i<this.needToDraw.nodes.length;i++){
          delete this.needToDraw.nodes[i].flated
        }
        this.$refs.drawTree.setGraphData(this.needToDraw, this.isDifferentColor);
      } else {

        let index1 = new Date().getTime().toString() + "_" + this.selectedSQL + "_" + val.number;
        let best = null;
        for(let t of this.tableData){
          if (t.number === 0) {
            best = t;
          }
        }

        for(let i =0;i<val.tree.nodes.length;i++){
          delete val.tree.nodes[i].flated
        }
        for(let i =0;i<best.tree.nodes.length;i++){
          delete best.tree.nodes[i].flated
        }

        let jsonStr = JSON.stringify([best.tree, val.tree]);
        localStorage.setItem(index1, jsonStr);
        localStorage.setItem(index1+"diffColor", this.isDifferentColor);

        let routerJump = this.$router.resolve({path: "/compareTree", query:{index: index1}});
        window.open(routerJump.href);
      }
    },

    generateDiffInfo(content) {
      let tempScanInfo = this.generateScanInfo(content);
      let scanInfo = {}
      for(let temp of tempScanInfo) {
        for(let key in temp) {
          scanInfo[key] = temp[key];
        }
      }

      let tree = this.generateJoinTree(content);
      let joinInfo = this.generateJoinInfo(tree);
      let res = {};
      res["scanInfo"] = scanInfo;
      res["joinInfo"] = joinInfo;
      return res
    },

    generateScanInfo(content) {
      let temp = content;
      if (Object.keys(temp).length === 0){
        return [];
      }

      if (temp.tag === "SCAN"){
        let c = temp.child;
        let t2n = {};
        t2n[c[0].name] = [temp.name, c[0].id];
        return [t2n];
      }

      let res = [];
      for (let c of temp.child) {
        let tempRes = this.generateScanInfo(c);
        if (tempRes.length > 0){
          res = res.concat(tempRes)
        }
      }
      return res;
    },

    generateJoinInfo(joinTree) {
      let res = {level: {}, map: {}};
      let depth= 1;
      let queue = [joinTree];

      while(queue.length !== 0) {
        let size = queue.length;
        res.level[depth] = [];
        for(let i=0;i<size;i++){

          let top = queue.shift();

          if (top.tag === "JOIN") {

            res.level[depth].push(top.id);


            let childId = [];
            for (let c of top.child) {
              childId.push(c.id);
              queue.push(c);
            }
            res.map[top.id] = childId;
          }
        }
        depth++;
      }

      depth--;
      delete res.level[depth];
      for(let i=1,j=depth-1;i<j;i++, j--) {
        let temp = res.level[i];
        res.level[i] = res.level[j];
        res.level[j] = temp;
      }

      return res;
    },

    generateJoinTree(content) {
      let temp = content;
      if (Object.keys(temp).length === 0){
        return {};
      }

      if (temp.tag === "TABLE") {
        return {tag: temp.tag, id: temp.id, child: []}
      }

      if (temp.tag === "JOIN") {
        let res = {tag: temp.tag, id: temp.id, child: []};

        for(let c of temp.child) {
          let tempRes = this.generateJoinTree(c);
          if (Object.keys(tempRes).length > 0) {
            res.child.push(tempRes)
          }
        }
        return res;
      }

      if (temp.child.length > 1) {
        return {};
      } else {
        return this.generateJoinTree(temp.child[0]);
      }
    },

    changeEarlyAccess(state){
      localStorage.setItem("isEarlyAccess", state.toString());
    },

    clearCookie(name) {
      let d = new Date();
      d.setTime(d.getTime() + (-1*24*60*60*1000));
      let expires = "expires="+d.toUTCString();
      document.cookie = name + "=" + "" + "; " + expires;
    }
  },
  mounted() {
    this.getSQLFiles();
    this.getDBInfo();
    localStorage.setItem("isEarlyAccess", "false");
    localStorage.setItem("formInfo", "");
  }
}
</script>

