<template>
  <div>
    <div v-loading="g_loading" style="width: calc(100% - 10px);height:calc(100vh - 140px);">
      <SeeksRelationGraph ref="seeksRelationGraph" :options="graphOptions" :on-node-click="handleClickNode"/>
    </div>
  </div>
</template>

<script>
import SeeksRelationGraph from 'relation-graph'
export default {
  name: 'DrawTree',
  components: { SeeksRelationGraph },
  data() {
    return {
      g_loading: true,
      graphOptions: {
        'layouts': [
          {
            'label': '中心',
            'layoutName': 'tree',
            // 'layoutClassName': 'seeks-layout-tree',
            'defaultJunctionPoint': 'border',
            'defaultNodeShape': 0,
            'defaultLineShape': 1,
            // 'centerOffset_x':0,
            'centerOffset_y': 0,
            'min_per_width': '60',
            'min_per_height': '150',
            'max_per_height': '300',
          }
        ],
        'allowShowMiniToolBar': false,
        "defaultExpandHolderPosition": "bottom",
        'defaultLineShape': 4,
        'defaultJunctionPoint': 'tb',
        'defaultNodeShape': 1,
        'defaultNodeWidth': '50',
        'defaultNodeHeight': '250',
        'defaultNodeBorderWidth': 0,
        'defaultNodeColor': "#f8e3c5",
        'defaultNodeFontColor': '#606266',
        'defaultLineColor': '#606266',
        'moveToCenterWhenResize': false,
        'allowShowMiniNameFilter': false,
      }
    }
  },
  created() {
  },
  mounted() {
    this.setGraphData({'rootId':1, 'nodes':[{'id':1, 'text':'', 'color':"white", 'width':100, 'height':50}], 'links':[]}, false)
  },
  methods: {
    setGraphData(data, differentColor) {
      this.setDiffColor(data, differentColor);

      setTimeout(function() {
        this.g_loading = false
        this.$refs.seeksRelationGraph.setJsonData(data, (seeksRGGraph) => {
        })
      }.bind(this), 1000)
    },

    handleClickNode(nodeObject, $event){
      let temp = localStorage.getItem("isEarlyAccess");
      console.log(temp);
      if(temp === "false"){
        return;
      }

      if (nodeObject.data.info !== '' && nodeObject.data.info !== undefined){
        this.$notify({
          title: 'Index Info',
          message: nodeObject.data.info
        })
      } else {
        console.log("nothing to show: ", nodeObject.data.info)
      }
    },
  }
}
</script>

